import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { AppState } from 'src/@core/ngrx/app.state';
import { WorkOrder } from 'src/@core/ngrx/model/workorder.model';
import { GlobalService } from 'src/app/services/global.service';
import * as WorkorderActions from './../../../../@core/ngrx/actions/workorder.actions';
import { SignaturePad } from 'angular2-signaturepad';
import { SignaturePadModule } from 'angular2-signaturepad';

@Component({
  selector: 'app-workorder-detail',
  templateUrl: './workorder-detail.component.html',
  styleUrls: ['./workorder-detail.component.scss']
})
export class WorkorderDetailComponent implements OnInit {

  workOrder : any;

  workOrderStatus = new WorkOrderStatus();
  workStatusList: any[] = [];

  isLoading = false;
  status = { instantStatusId: null }

  constructor(private service: GlobalService, private router: ActivatedRoute,private store: Store<AppState>) {
    this.store.subscribe((resp: any) => {
      console.log('Store DETAY', resp)
      this.workOrder = resp.workOrders[0];
      this.workOrderStatus.workOrderId = parseInt(this?.workOrder?.id);
    })

    console.log('XXXXX' , this.workOrder)


    if (this.workOrder == undefined) {
      router.params.subscribe((resp) => {
        this.workOrderStatus.workOrderId = parseInt(resp?.id);

        service.Get(`/WorkOrders/${resp.id}`).subscribe((respw: any) => {
          console.log('data', respw)
          this.workOrder = respw.data;
        })
      })
    }


    // i detay apiden tm iler gibi gelmeli
    service.Get('/Status/getusertatuses').subscribe((resp:any)=>{
      console.log('--Statuses--', resp.data)
      this.workStatusList = resp.data;
    })
    
  }



  async send(fm: NgForm) {
   this.service.formValidationMessage(fm.form.controls);
   if (fm.form.invalid) { return }
   this.isLoading = true;

    console.log('work order' , this.workOrderStatus)
     this.service.Create('/WorkOrderStatus', this.workOrderStatus).subscribe((resp: any) => {
      this.service.createNotification('success', `<strong>  Emri Durumu eklendi</strong> `, ``);
      this.isLoading = false;
    }, error => {
      console.log('errr', error)
      this.service.createNotification('error', `Hata Olutu`, error.error.error.message)
      this.isLoading = false;
    }) 
  }
  
  ngOnInit(): void {
  }


  signaturePadOptions: Object = { 
    'minWidth': 2,
    'canvasWidth': 700,
    'canvasHeight': 300
  };

  signatureImg: string = '';
  @ViewChild(SignaturePad) signaturePad!: SignaturePad;

  ngAfterViewInit() {
    // this.signaturePad is now available
 //   this.signaturePad.set('minWidth', 2); 
 //   this.signaturePad.clear(); 
  }


  drawComplete() {
    console.log(this.signaturePad.toDataURL());
    this.workOrderStatus.signature = this.signaturePad.toDataURL();
  }

  drawStart() {
    console.log('begin drawing');
  }

  clearSignature() {
    this.signaturePad.clear();
    this.workOrderStatus.signature = null;
  }

  savePad() {
    const base64Data = this.signaturePad.toDataURL();
    this.signatureImg = base64Data;
  }


  // download
  download(dataURL:any, filename:any) {
    var blob = this.dataURLToBlob(dataURL);
    var url = window.URL.createObjectURL(blob);
  
    let a = document.createElement("a");
    //a{style = "display: none";
    a.href = url;
    a.download = filename;
  
    document.body.appendChild(a);
    a.click();
  
    window.URL.revokeObjectURL(url);
  }

  dataURLToBlob(dataURL:any) {
    // Code taken from https://github.com/ebidel/filer.js
    var parts = dataURL.split(';base64,');
    var contentType = parts[0].split(":")[1];
    var raw = window.atob(parts[1]);
    var rawLength = raw.length;
    var uInt8Array = new Uint8Array(rawLength);
  
    for (var i = 0; i < rawLength; ++i) {
      uInt8Array[i] = raw.charCodeAt(i);
    }
  
    return new Blob([uInt8Array], { type: contentType });
  }


  
  saveSVG(){
    if (this.signaturePad.isEmpty()) {
      alert("Please provide a signature first.");
    } else {
      var dataURL = this.signaturePad.toDataURL('image/svg+xml');
      this.download(dataURL, "signature.svg");
    }
  }



}


export class WorkOrderStatus{
  id = 0;
  createdDate =  new Date();
  endDate : Date;
  description: string;
  workOrderId : number;
  statusId : number;
  carId : number;
  signature?: any;
}